import React, { useState } from "react"
import {
    Box,
    Card,
    Checkbox,
    FormControl,
    FormControlLabel,
    Grid,
    IconButton,
    Radio,
    RadioGroup,
    Stack,
    ToggleButton,
    ToggleButtonGroup,
    Typography
} from "@mui/material"
import DriveFileRenameOutlineIcon from '@mui/icons-material/DriveFileRenameOutline';

import { WithdrawalSelect } from "../../../shared/ui";
import { FundsAmount } from "../../../shared/ui/FundsAmount";
import { DividedProgress } from "../../../shared/ui/DividedProgress";

type WithdrawalConfig = {
    method: string
    fromCurrency: number
    toCurrency: number
}

const currentBalance = 2600
const exchangeRate = 0.92307

export const WithdrawalPage = () => {
    const [config, setConfig] = useState<WithdrawalConfig>({
        method: 'bank',
        fromCurrency: 1300,
        toCurrency: 1200
    })

    const handleChangeConfig = (name: keyof WithdrawalConfig, value: unknown) => {
        setConfig((prevState) => {
            if (name === 'fromCurrency' && typeof value === 'number')  {
                return {
                    ...prevState,
                    fromCurrency: value,
                    toCurrency: value * exchangeRate
                }
            }

            if (name === 'toCurrency' && typeof value === 'number')  {
                return {
                    ...prevState,
                    toCurrency: value,
                    fromCurrency: value / exchangeRate
                }
            }

            return {
                ...prevState,
                [name]: value
            }
        })
    }

    return (
        <Stack spacing={2.5}>
            <Typography variant="h2">
                 
            </Typography>
            <ToggleButtonGroup
                exclusive={true}
                aria-label=" "
                onChange={(_, value) => {
                    if (value !== null) {
                        handleChangeConfig('method', value)
                    }
                }}
                value={config.method}
                size="medium"
            >
                <ToggleButton value="bank"></ToggleButton>
                <ToggleButton value="cash"></ToggleButton>
                <ToggleButton value="courier"></ToggleButton>
            </ToggleButtonGroup>
            <FormControl fullWidth={true}>
                <WithdrawalSelect />
            </FormControl>
            <Typography variant="h2">
                
            </Typography>
            <Grid container spacing={1}>
                <Grid item xs={12} sm={6}>
                    <Stack width="100%" spacing={1}>
                        <Typography variant="body2" color="text.secondary">
                             ( 1000)
                        </Typography>
                        <FundsAmount
                            value={config.fromCurrency}
                            fractionDigits={5}
                            currency="Ethereum, ETH"
                            max={currentBalance}
                            min={0}
                            onChange={(value) => handleChangeConfig('fromCurrency', value)}
                            step={1000}
                        />
                    </Stack>
                </Grid>
                <Grid item xs={12} sm={6}>
                    <Stack width="100%" spacing={1}>
                        <Typography variant="body2" color="text.secondary" sx={{ textAlign: { xs: 'left', sm: 'right' } }}>
                             ( 1000)
                        </Typography>
                        <FundsAmount
                            value={config.toCurrency}
                            currency=", RUB"
                            min={0}
                            onChange={(value) => handleChangeConfig('toCurrency', value)}
                            step={1000}
                        />
                    </Stack>
                </Grid>
            </Grid>
            <Box>
                <DividedProgress value={config.fromCurrency / currentBalance * 100} ariaLabel="  " />
            </Box>
            <Stack justifyContent="space-between" alignItems="center" direction="row" mt={1.5}>
                <Typography variant="h2" gutterBottom>
                    
                </Typography>
                <FormControlLabel
                    labelPlacement="start"
                    label=" "
                    slotProps={{ typography: { variant: 'body2', textAlign: 'right' }}}
                    control={
                        <Checkbox checked={true} />
                    }
                />
            </Stack>
            <Box>
                <Card variant="outlined" sx={{ pt: 1 }}>
                    <FormControl fullWidth={true} sx={{ mb: 1 }}>
                        <RadioGroup
                            aria-label="    "
                            defaultValue="card"
                            name="transfer-details"
                        >
                            <Stack width="100%" direction="row" justifyContent="flex-start" spacing={4} alignItems="center">
                                <FormControlLabel value="card" control={<Radio color="primary" />} slotProps={{ typography: { variant: 'body2' }}} label=" " />
                                <FormControlLabel value="number" control={<Radio color="primary" />} slotProps={{ typography: { variant: 'body2' }}} label=" " />
                            </Stack>
                        </RadioGroup>
                    </FormControl>
                    <Stack direction="row" width="100%" justifyContent="space-between" alignItems="center">
                        <Typography variant="body1" id="number-label" component="span">
                             
                        </Typography>
                        <IconButton aria-labelledby="number-label">
                            <DriveFileRenameOutlineIcon color="primary" />
                        </IconButton>
                    </Stack>
                </Card>
            </Box>
            <Box>
                <Card variant="outlined">
                    <Stack direction="row" width="100%" justifyContent="space-between" alignItems="center">
                        <Typography variant="body1" id="fio-label" component="span">
                             
                        </Typography>
                        <IconButton aria-labelledby="fio-label">
                            <DriveFileRenameOutlineIcon color="primary" />
                        </IconButton>
                    </Stack>
                </Card>
            </Box>
            <Box>
                <Card variant="outlined">
                    <Stack direction="row" width="100%" justifyContent="space-between" alignItems="center">
                        <Typography variant="body1" id="adddress-label" component="span">
                            
                        </Typography>
                        <IconButton aria-labelledby="adddress-label">
                            <DriveFileRenameOutlineIcon color="primary" />
                        </IconButton>
                    </Stack>
                </Card>
            </Box>
        </Stack>
    )
}