import {Box, Stack, Typography} from "@mui/material";
import React from "react";

type SegmentProps = {
    value: number
    title: string
}

const Segment = ({ value, title }: SegmentProps) => {
    return (
        <Stack spacing={0.4} width="100%">
            <Box
                position="relative"
                overflow="hidden"
                height="10px"
                width="100%"
                borderRadius={5}
                sx={{ backgroundColor: 'background.paper' }}>
                <Box
                    position="absolute"
                    height="100%"
                    width={`${value}%`}
                    sx={{ backgroundColor: 'primary.main' }}
                />
            </Box>
            <Typography variant="body2" color="text.secondary" textAlign="center">
                {title}
            </Typography>
        </Stack>
    )
}

type DividedProgressProps = {
    value: number
    ariaLabel: string
}

export const DividedProgress = ({ value, ariaLabel }: DividedProgressProps) => {
    const normalizedValue = Math.max(0, value)

    return (
        <Stack
            direction="row"
            width="100%"
            justifyContent="space-between"
            alignItems="center"
            spacing={0.5}
            aria-label={ariaLabel}
            role="progressbar"
            aria-valuenow={normalizedValue}
            aria-valuemax={100}
            aria-valuemin={0}
        >
            <Segment value={normalizedValue >= 25 ? 100 : normalizedValue * 4} title="min" />
            <Segment value={normalizedValue >= 50 ? 100 : Math.max(0, (normalizedValue - 25)) * 4} title="50%" />
            <Segment value={normalizedValue >= 75 ? 100 : Math.max(0 ,(normalizedValue - 50)) * 4} title="75%" />
            <Segment value={normalizedValue >= 100 ? 100 : Math.max(0, (normalizedValue - 75)) * 4} title="max" />
        </Stack>
    )
}