import {Box, Card, FormControl, IconButton, Stack, Typography} from "@mui/material";
import React, {KeyboardEvent} from "react";
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

type FundsAmountProps = {
    currency: string
    value: number
    step: number
    onChange: (value: number) => void
    fractionDigits?: number
    max?: number
    min: number
    name?: string
}

export const FundsAmount = ({ value, currency, min, onChange, step, name, max = Infinity, fractionDigits = 2 }: FundsAmountProps) => {
    const incrementValue = () => {
        const newValue = value + step

        if (newValue >= max) {
            onChange(Math.floor(max / step) * step)

            return
        }

        onChange(newValue)
    }

    const decrementValue = () => {
        const newValue = value - step

        if (newValue <= min) {
            onChange(min)

            return
        }

        onChange(newValue)
    }

    const handleFormOnKeyDown = (event: KeyboardEvent<HTMLDivElement>) => {
        if (event.key === 'ArrowRight') {
            incrementValue()
        }
        if (event.key === 'ArrowLeft') {
            decrementValue()
        }
    }

    const getFormattedValue = (value: number, fraction: number) => {
        return new Intl.NumberFormat('ru-RU', {
            minimumFractionDigits: fraction,
            maximumFractionDigits: fraction,
            useGrouping: true
        }).format(value).replace(/,/ug, '.')
    }

    return (
        <FormControl
            role="spinbutton"
            aria-valuemin={min}
            aria-valuemax={max}
            aria-valuenow={value}
            aria-label={`  ${currency}`}
            tabIndex={0}
            onKeyDown={handleFormOnKeyDown}
        >
            <input
                value={value}
                tabIndex={-1}
                name={name}
                aria-hidden={true}
                onChange={(event) => onChange(+event.target.value)}
                type="number"
                style={{
                    opacity: 0,
                    position: 'absolute',
                }}
            />
            <Card variant="outlined" sx={{ border: 'none', py: 1 }}>
                <Stack alignItems="center">
                    <Typography variant="body2" color="text.secondary">
                        {currency}
                    </Typography>
                    <Stack direction="row" justifyContent="space-between" alignItems="center" width="100%">
                        <Box>
                            <IconButton
                                size="small"
                                sx={{ backgroundColor: 'background.default' }}
                                onClick={decrementValue}
                            >
                                <RemoveIcon color="primary" aria-label=" " />
                            </IconButton>
                        </Box>
                        <Box flexGrow={1} display="flex" justifyContent="center" alignItems="center">
                            <Typography variant="body2" textAlign="center">
                                {getFormattedValue(value, fractionDigits)}
                            </Typography>
                        </Box>
                        <Box>
                            <IconButton
                                size="small"
                                sx={{ backgroundColor: 'background.default' }}
                                onClick={incrementValue}
                            >
                                <AddIcon color="primary" aria-label=" " />
                            </IconButton>
                        </Box>
                    </Stack>
                </Stack>
            </Card>
        </FormControl>
    )
}