import React from 'react';
import {
    Container,
    ThemeProvider,
    CssBaseline
} from '@mui/material';
import {WithdrawalPage} from "../pages/Withdrawal";
import {theme} from "./config/theme";

function App() {
  return (
      <ThemeProvider theme={theme}>
          <CssBaseline />
          <Container maxWidth="md">
              <WithdrawalPage />
          </Container>
      </ThemeProvider>
  );
}

export default App;